import {request} from "./request"

// 
export function getHomeMultidata() {
  return request({
    url:'/home/multidata'
  })
}


//    getparamsurl:/home/data?type=''&page=1
export function getHomeGoods(type,page) {
  return request({
    method: 'post',
    url: '/home/data',
    // postparams
    data: {
      type,
      page
    }
  })
}